                   }
               }).then((result) =>{
                //    console.log('result',result);
                //    this.tabsHeadData = result.data.data;
                    this.tabsHeadDataone = result.data.data.slice(0,1);
                    this.tabsHeadDatatwo = result.data.data.slice(2,3);
                    this.tabsHeadDatathree = result.data.data.slice(4,12);
                    this.tabsHeadDatafour = result.data.data.slice(13,14);
                    this.tabsHeadDatafive = result.data.data.slice(19,20);
                    this.tabsHeadDatasix = result.data.data.slice(24,26);
                    this.tabsHeadDataseven = result.data.data.slice(27,31);
              
                    this.tabsHeadData.push(...this.tabsHeadDataone,...this.tabsHeadDatatwo,...this.tabsHeadDatathree,...this.tabsHeadDatafour,...this.tabsHeadDatafive,...this.tabsHeadDatasix,...this.tabsHeadDataseven);
